import React from "react";
import Triangle from "../Triangle";
import FlexWrapperCenter from "../FlexWrapperCenter";
import { Box, Heading, Text, theme } from "@mechatronick/datum-design-system";
import { graphql, useStaticQuery } from "gatsby";
import StyledDivWithCoverControlledDimensions from "../StyledDivWithCoverControlledDimensions";
import Fade from "react-reveal/Fade";
import Pulse from "react-reveal/Pulse";
import SocialMedia from "../SharedComponents/SocialMedia";

const Header = () => {
  const { headerImage } = useStaticQuery(
    graphql`
        query {
            headerImage: file(relativePath: { eq: "backgroundIndex3.png" }) {
                childImageSharp {
                    fluid(maxWidth: 1366) {
                        ...GatsbyImageSharpFluid
                    }
                }
            }
        }
    `,
  );
  const headerImageSrc = headerImage.childImageSharp.fluid.src;
  return (
    <>
      <StyledDivWithCoverControlledDimensions
        smallScreenHeight={[60, 100]}
        mediumScreenHeight={[70, 85]}
        largeScreenHeight={[70, 90]}
        backgroundImageUrl={headerImageSrc}
      >
        <Triangle
          color="lightAccent"
          opacity={0.8}
          height={[40, 60, 80]}
          width={[80, 70, 60]}
        />
        <Triangle
          color="primary"
          opacity={0.8}
          height={[12, 17, 22]}
          width={[105, 110, 115]}
          invertX
          invertY
        />
        <Triangle
          color="lightPrimary"
          opacity={1}
          height={[10, 15, 20]}
          width={[100, 100, 100]}
          invertX
          invertY
        />
        <FlexWrapperCenter>
          <Box
            m={[2, 3, 4]}
            p={[3, 4, 6]}
            bg={"trueWhite"}
            style={{ boxShadow: `${theme.boxShadows[1]}`, textAlign: "center" }}
          >
            <Text
              color={"slate"}
              f={[2, 3, 4]}
            >Welcome to</Text>
            <Pulse>
              <Heading.h1
                f={[5, 5, 7]}
                color={"primary"}
              >Datum Consulting NZ</Heading.h1>
            </Pulse>
            <br/>
            <Fade delay={750} left>
              <Box>
                <Text.span
                  f={[3, 4, 6]}
                >We</Text.span>
                {" "}
                <Text.span
                  color={"primary"}
                  bold
                  f={[3, 4, 6]}
                >
                  craft strategy and software.
                </Text.span>
              </Box>
            </Fade>
            <Fade delay={2000} right>
              <Box>
                <Text.span
                  f={[3, 4, 6]}
                >To</Text.span>
                {" "}
                <Text.span
                  color={"accent"}
                  bold
                  f={[3, 4, 6]}
                >
                  meet your unique needs.
                </Text.span>
              </Box>
            </Fade>
            <Fade delay={3000}>
              <SocialMedia/>
            </Fade>
          </Box>
        </FlexWrapperCenter>
      </StyledDivWithCoverControlledDimensions>
      <StyledDivWithCoverControlledDimensions
        smallScreenHeight={[10]}
        mediumScreenHeight={[15]}
        largeScreenHeight={[20]}
      >
        <Triangle
          color="lightPrimary"
          opacity={1}
          height={[10, 15, 20]}
          width={[100, 100, 100]}
        />
      </StyledDivWithCoverControlledDimensions>
    </>
  );
};

export default Header;